.button {
  display: inline-flex;
  gap: 0.5rem;
  background-color: $primary;
  color: $secondary;
  justify-content: center;
  align-items: center;
  border: 1px solid transparent;
  box-shadow: 0px 4px 12px 2px rgba(0, 0, 0, 0.15);
  padding: 16px 24px;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
}
